<template>
    <transition name='fade'>
      <div class='draftPage'>
        <v-Header class="head">
          <span slot='text'>我的草稿</span>
        </v-Header>
        <div class='draftScroll'>
          <div class='draftListsCon' v-for='item in lists' :key='item.contId' @click="gotopath(item.contId)">
            <div class='draftList'>
              <div class='listImg' v-if='item.preimg' :style="{backgroundImage:'url('+item.preimg+')'}"></div>
              <div class='listInfo'>
                <span class='type'>{{showUserType(item.contType)}}</span>
                <span class='text' v-text='item.title'></span>
                <span class='time' v-text='item.createDate'></span>
              </div>
            </div>
          </div>
          <div class='Tips' v-if='lists.length==0'>暂无草稿</div>
        </div>
      </div>
    </transition>
</template>
<script>
import Header from '../../components/header';
export default {
    name:'',
    props:[],
    components: {'v-Header':Header},
    data(){
        return {
            lists:[]
        }
    },
    methods: {
      gotopath(data){
        this.$router.push({path:'/write/preview',query:{productId:data}});
      },
      showUserType(data){
        switch (data) {
          case 1:
            return '文章';
            break;
          case 2:
            return '贴子';
            break;
          case 3:
            return '约';
            break;
          case 4:
            return '装备';
            break;
          case 5:
            return '教程';
            break;
          default:
            break;
        }
      }
    },
    created(){
        if(this.loadType == 'all') return;
        this.$http.get(this.$port.MY_CONT_DRAF,{
          params:{}
        }).then((response) => {
          const {data} = response;
          if(response.code == 200){
            this.lists=this.lists.concat(data);
            this.loadType = 'all';
          }
        })
        .catch((error) => {
          this.loading = false;
          console.log(error.response.data);
        });
			},
}
</script>

<style lang="scss" scoped>
    *{
      border: none;
      margin: 0;
      padding: 0;
    }
    .draftPage{
      width:100%;
      height:100%;
      position: absolute;
      top:0;
      left:0;
      overflow: hidden;
      background:white;
      .draftScroll{
        position: absolute;
        top:0.82rem;
        left:0;
        right:0;
        bottom:0;
        margin:auto;
        overflow-x: hidden;
        overflow-y: scroll;
        -webkit-overflow-scrolling:touch;
        .draftListsCon{
          width:100%;
          height:1.5rem;
          border-bottom: 0.01rem solid rgb(229,229,229); 
          display:flex;
          align-items:center;
          justify-content: center;
          .draftList{
            width:6.9rem;
            height:1.2rem;
            display:flex;
            .listImg{
              width:1.2rem;
              height:1.2rem;
              background-size:cover;
              background-position:center;
              margin-right:0.38rem;
            }
            .listInfo{
              flex:1;
              display: flex;
              flex-direction: column;
              justify-content:space-between;
              overflow: hidden;
              line-height: initial;
              .type{color:#1797c6;font-size:0.24rem;}
              .text{color:#333333;font-size:0.3rem;overflow:hidden;white-space: nowrap;
              text-overflow: ellipsis;}
              .time{color:#888888;font-size:0.24rem;}
            }
          }
        }
      }
      .Tips{
        text-align: center;
      }
    }
</style>


